<script lang="ts">
export default {
  name: 'AppBreadcrumb',
}
</script>

<script setup lang="ts">
// 面包屑导航： 优先显示外界传入的路径，如果没有传入路径，则使用当前路由的名称
import { computed } from 'vue'
import { useRoute } from 'vue-router'

const route = useRoute()

const props = defineProps<{
  breadcrumbPath?: string[] // 面包屑导航路径
}>()

const breadcrumbs = computed(() => {
  if (props.breadcrumbPath?.length) {
    return props.breadcrumbPath
  }

  // 如果没有传入路径，使用当前路由名称
  return [
    typeof route.meta.title === 'function' ? route.meta.title(route) : (route.meta.title as string),
  ]
})
</script>

<template>
  <el-space class="app-breadcrumb">
    <span class="color-regular">当前位置：</span>
    <el-breadcrumb>
      <el-breadcrumb-item v-for="item in breadcrumbs" :key="item">
        {{ item }}
      </el-breadcrumb-item>
    </el-breadcrumb>
  </el-space>
</template>

<style scoped lang="less">
.app-breadcrumb {
  margin-bottom: 16px;
  padding: 16px;
  border-bottom: 1px solid #e4e7ed;

  :deep(.el-breadcrumb) {
    .el-breadcrumb__item:nth-last-child(1) {
      .el-breadcrumb__inner {
        color: #000;
      }
    }
  }
}
</style>
